import React, { useState } from 'react';
import Cookies from 'js-cookie';
import { Link, Redirect } from 'react-router-dom';
import {
  Button,
  Input,
  PasswordInput,
} from '@ya.praktikum/react-developer-burger-ui-components';
import classNames from 'classnames';
import Loader from '../Icons/Loader';
import { UserActionsCreator } from '../../services/actions/user/user-actions';
import styles from '../LoginForm/styles.module.css';
import { useAppDispatch } from '../../services';

declare module 'react' {
  interface FunctionComponent<P = {}> {
    (props: PropsWithChildren<P>, context?: any): ReactElement<any, any> | null;
  }
}
//       ,      .
function RegisterForm() {
  const dispatch = useAppDispatch();
  const [loader, setLoader] = useState<boolean>(false);

  const [form, setValue] = useState<{
    name: string;
    email: string;
    password: string;
  }>({ name: '', email: '', password: '' });

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setValue({ ...form, [e.target.name]: e.target.value });
  };

  const submitForm = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoader(true);
    await UserActionsCreator.register(form)(dispatch);

    setLoader(false);
  };

  if (Cookies.get('accessToken')) {
    <Redirect to={'/profile'} />;
  }

  return (
    <div className={styles.profileForm}>
      <div
        className={classNames(
          styles.profile_form__title,
          'mb-6 text text_type_main-medium'
        )}
      >
        
      </div>
      <form onSubmit={submitForm}>
        <div className={classNames(styles.profile_form__group, 'mb-6')}>
          <Input
            type={'text'}
            placeholder={''}
            name={'name'}
            value={form.name}
            errorText={''}
            onChange={onChange}
          />
        </div>
        <div className={classNames(styles.profile_form__group, 'mb-6')}>
          <Input
            type={'email'}
            placeholder={'E-mail'}
            name={'email'}
            value={form.email}
            errorText={''}
            onChange={onChange}
          />
        </div>
        <div className={classNames(styles.profile_form__group, 'mb-6')}>
          <PasswordInput
            name={'password'}
            value={form.password}
            onChange={onChange}
          />
        </div>
        <div className={classNames(styles.profile_form__group, 'mb-20')}>
          <Button type="primary" size="medium">
            {loader ? <Loader /> : ''}
          </Button>
        </div>
      </form>
      <div className={classNames(styles.profile_form__group, 'mb-4')}>
        <p
          className={classNames(
            styles.color_text,
            'text text_type_main-default'
          )}
        >
           ?
        </p>
        <Link to="/login" className="color_link">
          
        </Link>
      </div>
    </div>
  );
}
export default RegisterForm;
